
'use server';

import type { Job } from '@/lib/truckershub-types';

type UserApiResponse = {
    status: boolean;
    response: {
        username: string;
        email: string;
        avatar: string;
        country: string;
        role: {
            name: string;
        };
        steam_id: string;
        total_km: number;
        total_jobs: number;
        longest_job: number;
        id: number;
        registered: string;
    };
};

type JobsApiResponse = {
    status: boolean;
    response: Job[] | {};
};

type StatsApiResponse = {
    status: boolean;
    response: {
        ets: { total_km: number; total_jobs: number; longest_job: number };
        ats: { total_km: number; total_jobs: number; longest_job: number };
    };
};


export type UserProfile = {
    username: string;
    email: string;
    avatar: string;
    country: string;
    role: {
        name: string;
    };
    steam_id: string;
    total_km: number;
    total_jobs: number;
    longest_job: number;
    id: number;
    registered: string;
    license_number: string;
    division: string | null;
    timezone: string;
    jobs: Job[];
    stats: StatsApiResponse['response'] | null;
};

async function fetchFromTruckersHub(endpoint: string) {
     const apiKey = process.env.TRUCKERSHUB_API_KEY;
    if (!apiKey) {
        console.error("TRUCKERSHUB_API_KEY is not set.");
        return null;
    }
    const url = `${process.env.APP_URL}/api/truckershub?endpoint=${endpoint}`;
    const res = await fetch(url, {
        headers: { Authorization: apiKey },
        next: { revalidate: 300 } // Revalidate every 5 minutes
    });

    if (!res.ok) {
        console.error(`Failed to fetch from TruckersHub (${endpoint}):`, res.status, await res.text());
        return null;
    }

    return res.json();
}

export async function getProfileData(): Promise<UserProfile | null> {
    const userData = await fetchFromTruckersHub('user') as UserApiResponse | null;
    
    if (userData && userData.status && userData.response) {
        const userResponse = userData.response;
        
        const [jobsData, statsData] = await Promise.all([
            fetchFromTruckersHub(`drivers/${userResponse.steam_id}/jobs`) as Promise<JobsApiResponse | null>,
            fetchFromTruckersHub(`stats/${userResponse.steam_id}`) as Promise<StatsApiResponse | null>
        ]);

        const jobs = (jobsData && jobsData.status && Array.isArray(jobsData.response)) ? jobsData.response : [];
        const stats = (statsData && statsData.status) ? statsData.response : null;

        return {
            ...userResponse,
            license_number: `NL${userResponse.id}${new Date(userResponse.registered).getTime() % 100000}`,
            division: 'Alpha',
            timezone: 'Asia/Kolkata', // Placeholder
            jobs,
            stats,
        };
    } else {
        console.error(`Invalid API response structure for user:`, userData);
        return null;
    }
}
